body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;
  letter-spacing: 1px;
}
.App {
  background-image: url(/To-Do-App/static/media/thunderstorm.3f83c751.jpg);
  background-position: center;
  background-size: cover;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
label{
  position: relative;
  height: 70px;
  opacity: .7;
  background: rgba(255,255,255);
  border-radius: 5px;
  overflow: hidden;
  max-width: 800px;
  width: 90vw;
  transition: .2s;
  display: -webkit-flex;
  display: flex;
  box-shadow: 10px 10px 20px 1px rgba(0, 0, 0, 0.322);
}
label.active{
  opacity: 1;
}
section{
  position: absolute;
  padding: 5px 20px;
  height: 100%;
  top: 50%;
  -webkit-transform: translateY(-30%);
          transform: translateY(-30%);
  opacity: 1;
  transition: .3s cubic-bezier(0.18, 0.58, 0.59, 1.04);
  z-index: 1;
  font-size: 20px;
}
input{
  padding-left: 50px;
  transition: .3s cubic-bezier(0.18, 0.58, 0.59, 1.04);
  position: relative;
  height: 100%;
  width: 100%;
  z-index: 2;
  border:none;
  font-size: 20px;
  background: transparent;
  z-index: 1000;

}

input:focus{
  padding-left: 20px;
}
input:focus ~ section{
  padding: 5px 10px;
  opacity: 0;
}
button{
  cursor: pointer;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-family:'Average Sans', sans-serif;
  color:snow;
  border: none;
  padding: 10px 15px;
  margin: 5px;
  border-radius: 3px;
  opacity: .8;
  transition: .2s;
  background-image: linear-gradient(#0adba1,#8dd859)
}
button:hover,
label:hover{
  opacity: 1;

}
input[type="text"], input[type="password"], button, textarea, select { 
  outline: none;
}
h3.selectName{
  font-size: 35px;
  letter-spacing: 3px;
  position: absolute;
  top: 30%;
  font-weight: 400;
  text-shadow: 4px 4px 1px black;
  color: aliceblue;
}
h3.welcome{
  margin-bottom: 40px;
  font-size: 28px;
  color: snow;
  font-weight: 400;
  text-shadow: 4px 4px 1px black;
}

div.cont{
  display: -webkit-flex;
  display: flex;
  max-width: 800px;
  width: 70vw;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  max-width: 800px;
  width: 90vw;
}
ul{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
div.selectingWallpaper{
  color: snow;
  letter-spacing: 1px;
  font-size: 12px;
  position: fixed;
  z-index: 11;
  bottom: 10px;
  text-transform: uppercase;
  cursor: pointer;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
div.selectingWallpaper p:nth-child(1){
  font-size: 17px;
}

div.wallpapers{
  position: fixed;
   height: 100vh;
  width: 100vw;
  background: rgba(0, 0, 0, 0.425);
  z-index: -10;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: space-around;
          align-content: space-around;
}
div.wallpapers div{
  height: 30%;
  -webkit-flex-basis: 30%;
          flex-basis: 30%;
  border-radius: 4px;
  background-position: center;
  background-size: cover;
  box-shadow: 10px 10px 55px 2px rgba(0, 0, 0, 0.5);
}
div.wallpapers div:nth-child(1){
  background-image: url(/To-Do-App/static/media/Bridge.c82b34b9.jpg);
}

div.wallpapers div:nth-child(2){
  background-image: url(/To-Do-App/static/media/sky.1aacd0d3.jpg);

}
div.wallpapers div:nth-child(3){
  background-image: url(/To-Do-App/static/media/bubble.47afcf6b.jpg);

}
div.wallpapers div:nth-child(4){
  background-image: url(/To-Do-App/static/media/bubblesSoap.c53df244.jpg);

}
div.wallpapers div:nth-child(5){
  background-image: url(/To-Do-App/static/media/lightbulb.db3bc4d3.jpg);

}
div.wallpapers div:nth-child(6){
  background-image: url(/To-Do-App/static/media/thunderstorm.3f83c751.jpg);

}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

li {
    position: relative;
    font-size: 16px;
    background: #FFFFFF;
    border-radius: 3px;
    margin: 4px 0px;
    max-width: 800px;
    width: 90vw;
    list-style: none;
    height: 40px;
    opacity: .9;
    box-shadow: 10px 10px 20px 1px rgba(0, 0, 0, 0.322);

}



.icons {
    display: -webkit-flex;
    display: flex;
    padding: 5px 0;
    margin-right: 10px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-basis: 10%;
            flex-basis: 10%;
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.fa-check-circle,
.fa-star {
    cursor: pointer;
    transition: color .2s, -webkit-transform .4s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    transition: transform .4s cubic-bezier(0.455, 0.03, 0.515, 0.955), color .2s;
    transition: transform .4s cubic-bezier(0.455, 0.03, 0.515, 0.955), color .2s, -webkit-transform .4s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    font-size: 17px;
    padding-left: 5px;
    position: relative;
    top: 25%;
    -webkit-flex-basis: 30px;
            flex-basis: 30px;
    display: block;

}

.fa-check-circle:hover {
    color: green;
}

.fa-star:hover {
    color: rgb(231, 231, 48);
}

p.work {
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

button.remove {
    background: transparent;
    border-radius: 50%;
    color: black;
    height: 25px;
    width: 25px;
    padding: 1px;
    opacity: 0;
    transition: .2s linear;
    cursor: pointer;
}
li:hover > div.icons button.remove {
    opacity: 1;
    -webkit-transform: scale(1.1);
            transform: scale(1.1)
}

h4,
span {
    color: snow;
    font-size: 15px;
    font-weight: 400;
    letter-spacing: 2px;
    text-shadow: 3px 3px 1px black;
    padding: 5px 0;
}

span.reset {
    text-align: right;
    cursor: pointer;

}

span.reset p {
    display: inline;
}

span.reset span {
    color: rgb(255, 7, 7);
    font-weight: bolder;
    text-shadow: 1px 1px 7px rgba(216, 139, 139, 0.363);
    padding-left: 10px;

}

@media (max-width:420px) {

    h4,
    span,
    p,
    button,
    .fa-check-circle,
    .fa-star,
    i
    {
        font-size: 12px;
    }
    ::-webkit-input-placeholder{
        font-size: 10px;
    }
    ::-ms-input-placeholder{
        font-size: 10px;
    }
    ::placeholder{
        font-size: 10px;
    }
    h3.welcome {
        font-size: 20px;
    }
}

